<template lang="html">
<div>
  <h1>Edit Post</h1>
  <Editor :content="content" mode="edit"></Editor>
</div>
</template>

<script>
import Editor from "./Editor.vue"
export default {
  data() {
    return {
      content: ''
    }
  },
  components: {
    'Editor': Editor
  },
  created() {
    console.log(this.$route.params.id);
    fetch(`http://localhost:8080/api/blogs/${this.$route.params.id}/edit`)
    .then(res => res.json())
    .then(json => this.content = json.body)
    .catch(err => console.error(err))
  }
}
</script>

<style lang="css">
</style>



